"use client";

import { useState } from "react";
import dynamic from "next/dynamic";

const Editor = dynamic(() => import("@/components/Editor"), {
    ssr: false,
    loading: () => <div className="h-[300px] w-full bg-gray-50 border border-gray-200 rounded-xl animate-pulse" />
});
import ButtonPrimary from "@/components/ButtonPrimary";
import { fetchAPI } from "@/lib/api";
import { useRouter } from "next/navigation";
import toast, { Toaster } from 'react-hot-toast';

export default function CreateBlog() {
    const router = useRouter();
    const [title, setTitle] = useState("");
    const [slug, setSlug] = useState("");
    const [excerpt, setExcerpt] = useState("");
    const [category, setCategory] = useState("Tech");
    const [content, setContent] = useState("");
    const [loading, setLoading] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        try {
            await fetchAPI('/blogs', {
                method: 'POST',
                body: JSON.stringify({
                    title,
                    slug,
                    excerpt,
                    category,
                    content,
                    published: true, // Default to true for now
                }),
            });
            toast.success('Blog created successfully!');
            setTimeout(() => router.push('/admin/blogs'), 1500);
        } catch (error) {
            console.error('Failed to create blog', error);
            toast.error('Failed to create blog');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="max-w-4xl mx-auto">
            <Toaster />
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-black">Create New Blog</h1>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-gray-500 mb-2">Title</label>
                    <input
                        type="text"
                        value={title}
                        onChange={(e) => {
                            setTitle(e.target.value);
                            setSlug(e.target.value.toLowerCase().replace(/ /g, '-').replace(/[^\w-]+/g, ''));
                        }}
                        className="w-full px-4 py-3 bg-white border border-gray-200 rounded-lg focus:outline-none focus:border-primary font-bold text-lg"
                        placeholder="Enter blog title"
                        required
                    />
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-gray-500 mb-2">Slug</label>
                    <input
                        type="text"
                        value={slug}
                        onChange={(e) => setSlug(e.target.value)}
                        className="w-full px-4 py-3 bg-gray-50 border border-gray-200 rounded-lg focus:outline-none focus:border-primary font-mono text-sm text-gray-600"
                        placeholder="article-slug"
                        required
                    />
                </div>

                <div className="grid grid-cols-2 gap-6">
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-gray-500 mb-2">Category</label>
                        <select
                            value={category}
                            onChange={(e) => setCategory(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-gray-200 rounded-lg focus:outline-none focus:border-primary"
                        >
                            <option value="Tech">Tech</option>
                            <option value="Design">Design</option>
                            <option value="Career">Career</option>
                            <option value="Life">Life</option>
                        </select>
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-gray-500 mb-2">Excerpt</label>
                    <textarea
                        value={excerpt}
                        onChange={(e) => setExcerpt(e.target.value)}
                        className="w-full px-4 py-3 bg-white border border-gray-200 rounded-lg focus:outline-none focus:border-primary min-h-[100px]"
                        placeholder="Short description..."
                        required
                    />
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-gray-500 mb-2">Content</label>
                    <Editor content={content} onChange={setContent} />
                </div>

                <div className="flex justify-end pt-6">
                    <button
                        type="submit"
                        disabled={loading}
                        className="px-8 py-4 bg-dark text-white font-bold rounded-lg hover:bg-black transition-colors disabled:opacity-50"
                    >
                        {loading ? 'Publishing...' : 'Publish Article'}
                    </button>
                </div>
            </form>
        </div>
    );
}
